<template>
<div class="nong-payment">
    <NavBar class="nong-payment--bg"/>
    <div class="nong-payment--chinagpay">
        <van-loading 
            vertical
            type="spinner" 
            v-if="!srcUrl">加载中......</van-loading>
        <iframe 
            frameborder="0" 
            width="100%" 
            height="100%"
            :src="srcUrl"
            v-if="srcUrl"></iframe>
    </div>
</div>
</template>

<script>
import NavBar from '../navbar'
import { 
    Toast,
    Loading } from 'vant'
import { recharge } from '@/api'
import { resFilter } from '@/utils/tool' // 响应过滤工具函数
export default {
    name: 'payment',
    components: {
        [Loading.name]: Loading,
        NavBar
    },
    data() {
        return {
            accountCode: this.$route.params.anCode || '',
            orderAmount: this.$route.query.orderAmount || '0.00',
            srcUrl: ''
        }
    },
    created() {
        let href = window.location.href
        let hash = window.location.hash
        let redirectUrl = href.replace(hash, '#/home') || 'localhost:8080/#/home' // 重定向地址
        this.getEmbUrl(redirectUrl)
    },
    methods: {
        async getEmbUrl(redirectUrl) { // 获取 iframe 嵌入地址
            let embUrl = resFilter(
                await recharge(
                    {
                    code: this.accountCode,
                    orderAmount: this.orderAmount,
                    redirectUrl
                    }
                ),
                {}
            )
            JSON.stringify(embUrl).length === 2 && Toast.fail('页面跳转失败')
            this.srcUrl = embUrl.url || ''
        }
    }
}
</script>

<style lang="less">
.nong-payment {
    width: 100%;
    height: 100%;
    &--bg {
        background: url(../../assets/images/ssg/pass_word.png) no-repeat top;
        background-size: cover;
    }
    &--chinagpay {
        width: 100%;
        height: calc(100% - @base-size*4.6);
        display: flex;
        flex-flow: column nowrap;
        justify-content: space-around;
        align-items: center;
    }
}
</style>
