import { Toast } from 'vant'

/**
 * 响应过滤工具函数
 * @param {Object} response 接口返回完整信息
 * @param {*} dataFormat 保证不返回 null
 * @return {dataFormat} 根据传入判断返回类型，与接口data字段返回类型保持一致，默认 Array
 */
export function resFilter(response, dataFormat = []) {// 判断接口返回响应码
    if(response.code !== '0') {
        Toast.fail(response.msg)
        throw new Error(response.msg)
    }else {
        return response.data || dataFormat
    }
}